#selectlang-container {
  display: flex;
}

#selectlang-title {
  display: flex;
  justify-content: space-between;
}

#selectlang-title > #reset {
  font-weighft: bold;
  cursor: pointer;
  border-radius: 100%;
  border-width: 0;
  width: 1.5em;
